.blogger-body {
  background: rgb(241, 243, 245) none repeat scroll 0% 0%;
}
nav {
  .nav-blue-bg {
    background: #0F92EE;
  }
}

/**
* Margin & Padding Classes
*/

.ml-1 {
  margin-left: 1rem !important;
}

mt-1 {
  margin-top: 1rem !important;
}

.p-1 {
  padding: 1rem;
}

.m-1 {
  margin: 1rem !important;
}

/** 
 Vertically align a dev center
*/

.v-center-wrapper {
  display: table;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  .set-center {
    display: table-cell;
    vertical-align: middle;
  }
}

.font-size-12 {
  font-size: 12px;
}

.font-size-16 {
  font-weight: normal !important;
  font-size: 16px !important;
}

@media screen and (min-width: 200px) and (max-width:560px) {
  .show-on-large {
    display: none;
  }
}

@media screen and (min-width: 560px) and (max-width:1210px) {
  .show-on-large {
    display: none;
  }
}

@media screen and (min-width: 1380px) and (max-width:1600px) {
  .show-on-large {
    display: block;
  }
}
